.sidebar {
  position: sticky;
  top: 80px;
  flex: 0.2;
  border-radius: 10px;
  text-align: center;
  height: fit-content;
}

.sidebar__avatar {
  margin-bottom: 10px;
}

.sidebar__top {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid lightgray;
  border-bottom: 0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: white;
  padding-bottom: 10px;
}

.sidebar__top > img {
  margin-bottom: -20px;
  width: 100%;
  height: 60px;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.sidebar__stat {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.sidebar__stat > p {
  color: gray;
  font-size: 11px;
  font-weight: 600;
}

p.sidebar__statNumber {
  font-weight: bold;
  color: #0a66c2;
}
.sidebar__stats {
  padding: 10px;
  margin-bottom: 10px;
  background-color: white;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.sidebar__bottom {
  text-align: left;
  padding: 10px;
  border: 1px solid lightgrey;
  background-color: white;
  margin-top: 10px;
  border-radius: 10px;
}

.sidebar__top > h4 {
  color: gray;
  font-size: 14px;
}
.sidebar__top > h2 {
  font-size: 18px;
}
.sidebar__recentItem {
  display: flex;
  font-size: 13px;
  color: gray;
  font-weight: bolder;
  margin-bottom: 5px;
  padding: 5px;
  cursor: pointer;
}
.sidebar__recentItem:hover {
  background-color: whitesmoke;
  cursor: pointer;
  color: black;
  border-radius: 10px;
}
.sidebar__hash {
  margin-right: 10px;
  margin-left: 5px;
}
.sidebar__bottom > p {
  font-size: 13px;
  padding-bottom: 10px;
}
@media only screen and (max-device-width: 480px) {
  .sidebar__stat > p {
    color: gray;
    font-size: 9px;
    font-weight: 600;
  }

  p.sidebar__statNumber {
    font-weight: bold;
    color: #0a66c2;
  }
  .sidebar__stats {
    padding: 10px;
    margin-bottom: 10px;
    background-color: white;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }

  .sidebar__bottom {
    text-align: left;
    padding: 10px;
    border: 1px solid lightgrey;
    background-color: white;
    margin-top: 10px;
    border-radius: 10px;
  }

  .sidebar__top > h4 {
    color: gray;
    font-size: 10px;
  }
  .sidebar__top > h2 {
    font-size: 14px;
  }
  .sidebar__recentItem {
    display: flex;
    font-size: 10px;
    color: gray;
    font-weight: bolder;
    margin-bottom: 5px;
    padding: 5px;
    cursor: pointer;
  }
  .sidebar__recentItem:hover {
    background-color: whitesmoke;
    cursor: pointer;
    color: black;
    border-radius: 10px;
  }
  .sidebar__hash {
    margin-right: 10px;
    margin-left: 5px;
  }
  .sidebar__bottom > p {
    font-size: 11px;
    padding-bottom: 10px;
  }
}
