.header {
  display: flex;
  justify-content: space-evenly;
  border-bottom: 0.1px solid lightgray;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: white;
  width: 100%;
  top: 0;
  position: sticky;
  z-index: 999;
}
@media only screen and (max-device-width: 480px) {
  .header {
    display: flex;
    justify-content: space-between;
    border-bottom: 0.1px solid lightgray;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: white;
    width: 100%;
    top: 0;
    position: sticky;
    z-index: 999;
  }
  .header__left {
    display: flex;
  }
  .header__left > img {
    object-fit: contain;
    height: 40px;
    margin-right: 10px;
  }
  .header__search {
    padding: 10px;
    padding-right: 20px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    height: 22px;
    color: gray;
    background-color: #eef3f8;
  }
}
.header__left {
  display: flex;
}
.header__left > img {
  object-fit: contain;
  height: 40px;
  margin-right: 10px;
}
.header__search {
  padding: 10px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  height: 22px;
  color: gray;
  background-color: #eef3f8;
}

.header__search > input {
  outline: none;
  border: none;
  background: none;
}

.header__right {
  display: flex;
}
